import { TextInput, Textarea, Group, Button } from '@mantine/core';
import { useForm } from '@mantine/form';

function Contact() {
    const form = useForm({
        initialValues: {
            name: '',
            email: '',
            subject: '',
            message: '',
        },
        validate: {
            name: (value) => value.trim().length < 2,
            email: (value) => !/^\S+@\S+$/.test(value),
            subject: (value) => value.trim().length === 0,
        },
    });

    return (
        <div className='text-center' style={{ marginTop: '40px', position:'sticky' }} id="contact">
            <h3><u>Contact me</u></h3>
            <form onSubmit={form.onSubmit(() => { })} style={{ marginRight: '30px', position:'sticky' }}>

                <TextInput
                    label="Name"
                    placeholder="Your name"
                    name="name"
                    variant="filled"
                    {...form.getInputProps('name')}
                />
                <TextInput
                    label="Email"
                    placeholder="Your email"
                    name="email"
                    variant="filled"
                    {...form.getInputProps('email')}
                />

                <TextInput
                    label="Subject"
                    placeholder="Subject"
                    mt="md"
                    name="subject"
                    variant="filled"
                    {...form.getInputProps('subject')}
                />
                <Textarea
                    mt="md"
                    label="Message"
                    placeholder="Your message"
                    maxRows={10}
                    minRows={5}
                    autosize
                    name="message"
                    variant="filled"
                    {...form.getInputProps('subject')}
                />

                <Group position="center" mt="xl">
                    <Button type="submit" size="md">
                        Send message
                    </Button>
                </Group>
            </form>
        </div>
    );
}

export default Contact;